/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"
import Menu from "./Menu"
import Footer from "./Footer"
import ScrollUpButton from "react-scroll-up-button"
import Helmet from "react-helmet"
const Layout = ({ children }) => {
  const script_Settings = graphql`
    query scriptSettings {
        wpgraphql {
            globalSettings {
              globalSettings {
                googleTagManager
                googleAnalytics
                tawkLiveChat
              }
            }
          }
    }
`

  return (

    <>
      <StaticQuery
        query={script_Settings}
        render={(data) => {
          if (data.wpgraphql.globalSettings) {
            const item = data.wpgraphql.globalSettings.globalSettings;
            // console.log(item.tawkLiveChat);
            return (
              <Helmet>
                  {/* <script defer src={item.tawkLiveChat} type="text/javascript">
                  </script> */}
                  <script async type="text/javascript">
                    {item.tawkLiveChat}
                  </script>
                  <script defer type="text/javascript">
                    {item.googleTagManager}
                  </script>
                  <script defer type="text/javascript">
                    {item.googleAnalytics}
                  </script>
              </Helmet>
            )
          }
          return null
        }}
      />
      <Menu />
      <main>{children}</main>
      <Footer />
      <ScrollUpButton style={{ width: 40, height: 40 }} />
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
