import React from "react"
import { StaticQuery, graphql } from "gatsby"
import MenuItem from "./MenuItem"
import { Link } from "gatsby"
import Logo from './Logo'
const MENU_QUERY = graphql`

    fragment MenuItem on WPGraphQL_MenuItem {
        id
        label
        url
        title
        target
        cssClasses
    }

    query GETMAINMENU {
        wpgraphql {
            menuItems(where: {location: TOP}) {
              nodes {
                ...MenuItem
                childItems {
                  nodes {
                    ...MenuItem
                    childItems {
                      nodes {
                        ...MenuItem
                      }
                    }
                  }
                }
              }
            }
            generalSettings {
                url
            }
            
        }
    }

`

const Menu = () => {
  return (
      <header>
        <nav className="navbar navbar-expand-lg fixed-top navbar-light bg-light">
          <div className="container">
            <Link to="/" className="navbar-brand">
              <Logo />
            </Link>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon" />
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <StaticQuery
                query={MENU_QUERY}
                render={(data) => {
                  if (data.wpgraphql) {
                    const menuItems = data.wpgraphql.menuItems.nodes
                    const wordPressUrl = data.wpgraphql.generalSettings.url
                    return (
                      <ul className="navbar-nav ml-auto">


                        {
                          menuItems &&
                          menuItems.map((prop, index) => (
                            <li className={prop.cssClasses + ' nav-item'} key={index}>
                              <MenuItem key={prop.id} menuItem={prop} wordPressUrl={wordPressUrl} />
                              <div className="dropdown-menu">
                                {prop &&
                                  prop.childItems && prop.childItems.nodes &&
                                  prop.childItems.nodes.map((child, index) => {
                                    return (
                                      <div className="nav-item-sub" key={index}>
                                        <MenuItem key={child.id} menuItem={child} wordPressUrl={wordPressUrl} />
                                      </div>
                                    )
                                  })}
                              </div>
                            </li>
                          ))
                        }
                      </ul>
                    )
                  }
                  return null
                }}
              />

            </div>
          </div>
        </nav>
      </header>
  )
}

export default Menu
