import React from "react"
// import Logo from "../images/logo.png"
import MenuPro from "./Footer/MenuProject"
import MenuAbout from "./Footer/MenuAbout"
import MenuNew from "./Footer/MenuNew"
import { StaticQuery, graphql } from "gatsby"
// import FluidImage from "./FluidImage"
import contentParser from "gatsby-wpgraphql-inline-images"
import Logo from "./Logo"
const global_Settings = graphql`

    query globalSettings {
        wpgraphql {
            globalSettings {
              globalSettings {
                address
                email
                phone
                googleTagManager
                googleAnalytics
                copyright
                tawkLiveChat
                favicon{
                    sourceUrl
                    altText
                    imageFile {
                        childImageSharp {
                            fluid(maxHeight: 80, maxWidth: 180, quality: 50, cropFocus: CENTER) {
                                ...GatsbyImageSharpFluid_withWebp_tracedSVG
                            }
                        }
                    }
                }
                logo {
                    sourceUrl
                    altText
                    imageFile {
                        childImageSharp {
                            fluid(quality: 50) {
                                ...GatsbyImageSharpFluid_withWebp_tracedSVG
                            }
                        }
                    }
                }
              }
            }
          }
    }
`

const Footer = () => {
    
    const pluginOptions = {
        wordPressUrl: `https://admin.sharkhung.vn/`,
        uploadsUrl: `https://admin.sharkhung.vn/wp-content/uploads/`,
      }
      
      

    return (
        <StaticQuery
            query={global_Settings}
            render={(data) => {
                if (data.wpgraphql.globalSettings) {
                    const item = data.wpgraphql.globalSettings.globalSettings;
                    const content = item.copyright;
                    // console.log(item.tawkLiveChat);
                    return (
                        <>
                            <footer>
                            <div className="container">
                                <div className="row">
                                    <div className="col-md-3 col-xs-12">
                                        <div className="item">
                                            <div className="logo">
                                                {/* <FluidImage image={item.logo} alt="logo"/> */}
                                                {/* <img src={Logo}  alt="logo" /> */}
                                                <Logo></Logo>
                                            </div>
                                            <p>
                                                <i className="fa fa-map-marker"></i>Địa chỉ: {item.address}</p>
                                            <p><i className="fa fa-phone" aria-hidden="true"></i>Hotline: <span>{item.phone}</span></p>
                                            <p>
                                                <i className="fa fa-envelope" aria-hidden="true"></i>Email: <span>{item.email}</span>
                                            </p>
                                        </div>
                                    </div>
                                    <div className="col-md-3 col-xs-12">
                                        <div className="item">
                                            <h3>Công ty</h3>
                                            <MenuAbout />
                                        </div>
                                    </div>
                                    <div className="col-md-3 col-xs-12">
                                        <div className="item">
                                            <h3>Tìm hiểu thêm</h3>
                                            <MenuPro />
                                        </div>
                                    </div>
                                    <div className="col-md-3 col-xs-12">
                                        <div className="item">
                                            <h3>Nhận tin dự án</h3>
                                            <MenuNew />
                                        </div>
                                    </div>
                                </div>
                                <div className="cpr text-center">
                                    <div>{contentParser({content}, pluginOptions)}</div>
                                </div>
                            </div>
                        </footer>
                        
                        {/* {test2} */}
                        </>
                    )
                }
                return null
            }}
        />

    )
}

export default Footer
