import React from "react"
import { StaticQuery, graphql } from "gatsby"
import FluidImage from "./FluidImage"
const logo_Settings = graphql`
    query logoSettings {
        wpgraphql {
            globalSettings {
              globalSettings {
                logo {
                    sourceUrl
                    altText
                    imageFile {
                        childImageSharp {
                            fluid(quality: 50) {
                                ...GatsbyImageSharpFluid_withWebp_tracedSVG
                            }
                        }
                    }
                }
              }
            }
          }
    }
`

const Logo = () => {
      
    return (
        <StaticQuery
            query={logo_Settings}
            render={(data) => {
                if (data.wpgraphql.globalSettings) {
                    const item = data.wpgraphql.globalSettings.globalSettings;
                    return (
                        <FluidImage image={item.logo} alt="logo"/>
                    )
                }
                return null
            }}
        />

    )
}

export default Logo
